import React, { useState, useEffect } from "react"
import { Section, Avatar, Button, ButtonLink, Empty, Loading } from "components"
import { Form, List, Popconfirm, message } from "antd"
import styled from "styled-components"
import useQueryData from "helpers/hooks/useQueryData"
import { queryOtherPlatforms } from "queries/data"
import SelectInput from "components/forms/SelectInput"
import { useMutation } from "react-apollo"
import { mutateAddOtherPlatform, mutateDeleteOtherPlatform } from "queries/profile"
import { client, mobile, renderError, newClient } from "helpers"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { SubmitButton } from "formik-antd"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

const FieldRow = styled.div`
	position: relative;
	margin-bottom: 2em;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
	.delete {
		display: none;
		cursor: pointer;
		visibility: hidden;
		position: absolute;
		right: 0;
		border-radius: 50%;
		width: 25px;
		color: #ff9d00;
		height: 25px;
		z-index: 99;
		&:hover {
			background: #eee;
		}
	}
`

const profileClient = newClient()

function OtherPlatforms({ onOtherPlat, onEditMode, unauthorizedUser, i18n, ...props }) {
	const { loading, fetchUser } = props
	const { editMode, setEditMode } = onEditMode
	const { userOtherPlat, setUserOtherPlat } = onOtherPlat
	const [otherPlats, setOtherPlats] = useState([])
	const [isAdding, setIsAdding] = useState(false)

	const {
		data: { getOtherPlatformMasterList = [] },
	} = useQueryData(queryOtherPlatforms, "profileClient")

	const [addOtherPlatform] = useMutation(mutateAddOtherPlatform, {
		client: client.profileClient,
		onCompleted: () => {
			setIsAdding(false)
			setEditMode(false)
		},
		onError: (err, network) => renderError(err),
	})

	const [deleteOtherPlatform] = useMutation(mutateDeleteOtherPlatform, {
		client: client.profileClient,
		onCompleted: (data) => {
			setIsAdding(false)
			setEditMode(false)
		},
		onError: (err, network) => renderError(err),
	})

	const otherPlatOptions = getOtherPlatformMasterList.map((item) => ({
		value: item.pvid,
		label: item.name,
	}))

	const handleCancel = () => {
		setIsAdding(false)
		setEditMode(false)
		setOtherPlats(userOtherPlat)
	}

	const handleAddOtherPlatform = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
		}
		addOtherPlatform({ variables: { list: values } }).then(() => {
			fetchUser()
			message.success(i18n._(t`The platform item has been ${hasPvid ? "updated" : "added"}`), 2)
		})
		setSubmitting(false)
	}

	const handleDelete = (pvid) => {
		const filtered = otherPlats.find((item) => item.pvid === pvid) || {}
		deleteOtherPlatform({ variables: { pvidList: [filtered.pvid] } }).then(() => {
			fetchUser()
			message.success(i18n._(t`The platform item has been deleted`), 2)
		})
	}

	const renderData = () => {
		if (unauthorizedUser) {
			return otherPlats.length === 0 ? (
				<Empty cudyLogo description={<Trans>This tutor is exclusive to Cudy</Trans>} />
			) : (
				<List
					itemLayout="horizontal"
					dataSource={otherPlats}
					renderItem={({ source = {}, link }) => (
						<List.Item>
							<List.Item.Meta
								avatar={source && <Avatar src={source.iconLink || ""} size="large" />}
								title={(source && source.name) || ""}
								description={
									<a href={link} target="__blank">
										<Trans>See my profile &#8599;</Trans>
									</a>
								}
							/>
						</List.Item>
					)}
				/>
			)
		}

		return otherPlats.length === 0 ? (
			<Section centered paddingHorizontal={0} marginBottom={0}>
				<Empty description={<Trans>You haven't set any platforms yet</Trans>} />
				<Button icon="plus" type="primary" onClick={() => setEditMode("otherplatforms")}>
					&nbsp; <Trans>Add one</Trans>
				</Button>
			</Section>
		) : (
			<List
				itemLayout="horizontal"
				dataSource={otherPlats}
				renderItem={({ source = {}, link }) => (
					<List.Item>
						<List.Item.Meta
							avatar={source && <Avatar src={source.iconLink} size="large" />}
							title={source && source.name}
							description={
								<a href={link} target="__blank">
									<Trans>See my profile &#8599;</Trans>
								</a>
							}
						/>
					</List.Item>
				)}
			/>
		)
	}

	const handleAddNewField = () => {
		setIsAdding(true)
		setOtherPlats((otherPlatforms) => [...otherPlatforms, { otherPlatformPvid: "", link: "" }])
	}

	useEffect(() => {
		setOtherPlats(userOtherPlat)
	}, [userOtherPlat])

	if (loading) return <Loading />

	return (
		<Section paddingHorizontal={0} noPadding={mobile} marginBottom={0}>
			{/* <NotAvailable title="This feature will be available soon" /> */}

			{editMode === "otherplatforms" ? (
				isAdding ? (
					<Formik
						onSubmit={handleAddOtherPlatform}
						validate={validateOtherPlat(i18n)}
						initialValues={{ otherPlatformPvid: undefined }}
					>
						{({ handleSubmit }) => (
							<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
								<SelectInput
									name="otherPlatformPvid"
									label={<Trans>Name of platform</Trans>}
									placeholder={i18n._(t`Name of platform`)}
									options={otherPlatOptions}
									marginBottom="10px"
								/>
								<TextInput
									name="link"
									addonBefore="https://"
									label={<Trans>Profile's URL</Trans>}
									placeholder={i18n._(t`Your profile's URL`)}
								/>
								<SubmitButton block>
									<Trans>Save this platform</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<ButtonLink block onClick={handleCancel}>
									<Trans>Cancel</Trans>
								</ButtonLink>
							</Form>
						)}
					</Formik>
				) : (
					<>
						{otherPlats.map((item) => (
							<Formik key={item.pvid} onSubmit={handleAddOtherPlatform} initialValues={item}>
								{({ handleSubmit }) => (
									<FieldRow>
										<Form layout="vertical" onSubmit={handleSubmit}>
											<SelectInput
												name="otherPlatformPvid"
												label={<Trans>Name of platform</Trans>}
												placeholder={i18n._(t`Name of platform`)}
												options={otherPlatOptions}
												marginBottom="10px"
											/>
											<TextInput
												name="link"
												addonBefore="https://"
												label={<Trans>Profile's URL</Trans>}
												placeholder={i18n._(t`Your profile's URL`)}
											/>
										</Form>
										<SubmitButton type="primary" className="mb1em" block>
											&nbsp; <Trans>Update</Trans>
										</SubmitButton>{" "}
										&nbsp;{" "}
										<Popconfirm title={<Trans>Delete this platform?</Trans>} onConfirm={() => handleDelete(item.pvid)}>
											<ButtonLink icon="close" block>
												&nbsp; <Trans>Delete</Trans>
											</ButtonLink>
										</Popconfirm>
									</FieldRow>
								)}
							</Formik>
						))}
						<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
							&nbsp; <Trans>Add a new platform</Trans>
						</Button>
						<ButtonLink block icon="close" onClick={() => setEditMode(false)}>
							&nbsp; <Trans>Cancel</Trans>
						</ButtonLink>
					</>
				)
			) : (
				renderData()
			)}
		</Section>
	)
}

const validateOtherPlat = (i18n) => (values) => {
	const errors = {}
	if (!values.link) errors.link = i18n._(t`You have to fill in the URL link`)
	else if (values.link.length < 8) errors.link = "Are you sure this is the correct URL?"
	else if (
		values.link.includes("twitter") ||
		values.link.includes("facebook") ||
		values.link.includes("linkedin") ||
		values.link.includes("instagram")
	) {
		errors.link = i18n._(t`This section is not meant to fill in your social media accounts`)
	}
	return errors
}

export default withI18n()(OtherPlatforms)
